import React from 'react';
import "./styles.css";

import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import { Link, Switch, Route } from "react-router-dom";

import Gallery from './Pages/Gallery';
import PictureDescription from './Pages/PictureDescription';
import Contact from './Pages/Contact';
import About from './Pages/About';


function MakeNavBar(props) {
  return(
  <Navbar className="navbar" sticky="top" bg="white" expand="sm">
      <Container>
        <Link to="/">
          <Navbar.Brand className="text-uppercase font-weight-bold">Frank Rubin</Navbar.Brand>
        </Link>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav>
            <Link to="/" className="nav-link text-uppercase font-weight-bold">Gallery</Link>
            <Link to="/about" className="nav-link text-uppercase font-weight-bold">About</Link>
            <Link to="/contact" className="nav-link text-uppercase font-weight-bold">Contact</Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
  </Navbar>
  );
}

export default function App() { 
  return (
    <div className="App">
      <MakeNavBar />
      <Switch>
        <Route exact path='/' component={Gallery}></Route>
        <Route exact path='/about' component={About}></Route>
        <Route path='/contact' component={Contact}></Route>
        <Route path='/picture/:topicId' component={PictureDescription}></Route>
      </Switch>
    </div>
  );
}
